.container {
  display: flex;
  align-items: center;
  margin-bottom: 9px;
}
.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 10px;
  margin-left: 20px;
}

.name {
  color: #fff;
  font-size: 20px;
  line-height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
